/* ===== 第一频 切换 ===== */
.first .fp-tableCell{position: relative;}
.index-carousel{width:100vw; height:100vh;}
.index-carousel .el-carousel__arrow{border-radius: 0px;background-color:rgba(0,0,0,.5);width:80px;height: 80px;}
.index-carousel .el-carousel__arrow i{font-size:36px;}
.index-carousel .el-carousel__arrow--left{left:0;}
.index-carousel .el-carousel__arrow--right{right: 0;}
.index-carousel .el-carousel__item .el-image{width:100%;}
