.index-carousel .el-carousel__item.is-animating {-webkit-transition: -webkit-transform 1s ease-in-out;transition: -webkit-transform 1s ease-in-out;transition: transform 1s ease-in-out;transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;transition: transform 1s ease-in-out,-webkit-transform 1s ease-in-out;}
.index-carousel .el-carousel__indicators--horizontal{bottom: 50px;}
.index-carousel .el-carousel__indicator--horizontal{padding:0 15px;}
.index-carousel .el-carousel__button{width:12px;height:12px; border-radius:50%;}
.groupexhibitiondetail-souse{position: absolute; left:calc(50% - 16.5px); right: 0; bottom: 200px;z-index: 100;}
.groupexhibitiondetail-souse img{width:33px; height:47px;}
.homepage{width:700px;height:90px;position:absolute;top:calc(50vh - 45px);left:calc(50vw - 350px);z-index:99;}
.slogen{width:100%;height:100%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between}
